.category-breadcrumb {
    color: rgb(112, 112, 112);
    height: 40px;
    display: flex;
    align-items: center;
    height: 50px;
    letter-spacing: -0.5px;

    .category-link {
        color: rgb(16, 16, 16);
        font: 400 18px/30px sans-serif !important;
        margin-right: 5px;
        text-decoration: none;
        letter-spacing: -1.2px;
        align-items: center;
        justify-content: center;
    }
}

.blog-content-screen-container-no-side-bar {
    background-color: white;
    width: fit-content;
    height: fit-content;
    min-height: 100vh;
    min-width: 100vw;

    .article-embed-youtube-video {
        margin-top: 0px !important;
        width: calc(100%);
        //height: 550px !important;
        height: 485px !important;
        margin-top: 15px;

        @media screen and (min-width: 1400px) {
            height: 550px !important;
            //height: 485px !important;
        }

        @media screen and (min-width: 1200px) and (max-width: 1400px) {
            height: 470px !important;
            //height: 420px !important;
        }

        @media screen and (min-width: 992px) and (max-width: 1200px) {
            height: 398px !important;
            //height: 352px !important;
        }

        @media screen and (min-width: 768px) and (max-width: 992px) {
            height: 400px !important;
        }

        @media screen and (min-width: 576px) and (max-width: 768px) {
            height: 300px !important;
        }

        @media screen and (max-width: 576px) {
            width: calc(100vw - 30px);
            height: calc((100vw - 30px) * (9 / 16)) !important;
        }
    }

    .embed-cover-photo {
        width: 100%;
        height: auto;
        margin-top: 15px;
        object-fit: contain;
    }

    .blog-content-screen-container {
        .cover-container {
            padding: 0px !important;
            height: 300px;
            display: flex;

            .black-overlay {
                width: 100%;
                position: absolute;
                background: rgba(0, 0, 0, 0.65);
                height: 300px;
                z-index: 99;
            }

            img {
                width: 100%;
                object-position: center;
                height: 300px;
                object-fit: cover;
                align-items: center;
                z-index: 98;
            }

            h1.article-title {
                display: flex;
                text-align: center;
                font-family: inherit;
                font-size: 35px;
                font-weight: bold;
                justify-content: center;
                align-items: center;
                position: absolute;
                width: 100%;
                height: 300px;
                z-index: 100;
                color: white;
            }
        }

        .post-and-side-container-fluid {
            background-color: white;
            min-height: 100vh;
            width: 100vw;

            .post-and-side-container {
                height: 100%;
                padding: 0px !important;

                .post-and-side-row {
                    .post-container {
                        background-color: white;
                        padding: 45px 15px 30px 15px !important;

                        @media screen and (max-width: 576px) {
                            padding: 0px 15px 30px 15px !important;
                        }

                        @media screen and (max-width: 1200px) {
                            border-right-width: 0px;
                        }

                        .image-container {
                            width: 100%;
                            margin-top: 15px;
                            border: 3px solid black;

                            img {
                                width: 100%;
                                object-fit: cover;
                            }
                        }


                        .title-container {
                            margin-top: 40px;
                            letter-spacing: 0.3px;
                            font: 700 42px/55px sans-serif;
                            font-weight: bolder;
                            margin-bottom: 10px;
                            letter-spacing: -2px;

                            @media screen and (max-width: 576px) {
                                font: 700 33px/42px sans-serif;
                            }
                        }

                        .subtitle-container {
                            letter-spacing: -0.6px;
                            font: 500 26px/41px sans-serif;
                            color: black;
                            margin-bottom: 15px;
                        }

                        .date-container {
                            font: 300 18px/22px sans-serif;
                            font-weight: normal;
                            color: #171717;
                            letter-spacing: -0.6px;
                            margin-bottom: 15px;
                        }

                        article {
                            font: 400 21px/29px sans-serif;
                            letter-spacing: 0px;
                            color: black;

                            strong {
                                font: 700 21px/29px sans-serif;

                            }

                            a {
                                color: black;
                                font-weight: bolder;
                                text-decoration: underline;
                                cursor: pointer;
                            }

                            figure {
                                &:first-of-type {
                                    display: none;
                                }

                                img {
                                    width: 100%;
                                    object-fit: contain;
                                    display: block;
                                }
                            }

                            h1 {
                                margin: 5px 0px;
                                font: 600 40px/45px sans-serif;
                                color: black;
                                font-weight: bold;

                                &:first-child {
                                    display: none;
                                }

                                margin: 10px 0px;

                                * {
                                    margin: 10px 0px;
                                }
                            }

                            h2 {
                                letter-spacing: -1px;
                                font: 600 35px/40px sans-serif;
                                color: black;
                                font-weight: bold;

                                &:first-of-type {
                                    display: none;
                                }

                                margin: 10px 0px;

                                * {
                                    margin: 10px 0px;
                                }
                            }

                            h3 {
                                font: 600 30px/35px sans-serif;
                                color: black;
                                letter-spacing: -1.5px;
                                font-weight: bold;

                                &:first-of-type {
                                    display: none;
                                }

                                margin: 10px 0px !important;

                                * {
                                    margin: 10px 0px !important;
                                }
                            }

                            h4 {
                                font-family: sans-serif;
                                margin: 10px 0px;
                                font-weight: bold;

                                * {
                                    font-family: sans-serif;
                                    margin: 10px 0px;
                                    font-weight: bold;
                                }
                            }

                            h5 {
                                font-family: sans-serif;
                                margin: 10px 0px;
                                font-weight: bold;

                                * {
                                    font-family: sans-serif;
                                    margin: 10px 0px;
                                    font-weight: bold;
                                }
                            }

                            h6 {
                                font-family: sans-serif;
                                margin: 10px 0px;
                                font-weight: bold;

                                * {
                                    font-family: sans-serif;
                                    margin: 10px 0px;
                                    font-weight: bold;
                                }
                            }

                            p {
                                color: rgb(19, 19, 19);
                                font: 400 22px/41px sans-serif;
                                letter-spacing: -0.5px;
                                width: 100%;
                                height: auto;

                                a {
                                    font: 400 22px/41px sans-serif;
                                    color: black;
                                    font-weight: bold;
                                    letter-spacing: -1px;
                                }
                            }

                            ul {
                                margin-top: 40px;
                                margin-bottom: 40px;
                                font-family: sans-serif;

                                li {
                                    font: 400 22px/41px sans-serif !important;
                                    color: black;
                                    font-weight: bold;
                                    letter-spacing: -1px;

                                    a {
                                        font: 400 22px/41px sans-serif !important;
                                        color: black;
                                        font-weight: bold;
                                        letter-spacing: -1px;
                                    }
                                }
                            }

                            figure {
                                font: 400 15px/41px sans-serif !important;
                                color: gray;
                                font-weight: bold;

                                figcaption {
                                    text-align: center;
                                    width: '100%'
                                }
                            }

                            strong {
                                color: rgb(0, 0, 0);
                                font-weight: bold;
                                font-family: sans-serif;
                            }

                            pre {
                                //padding: 15px;
                                overflow: scroll;
                                //background-color: black;
                                background-color: white;
                                height: fit-content;
                                max-width: 100%;
                                color: white;
                                //-ms-overflow-style: none;
                                /* IE and Edge */
                                scrollbar-width: 1px;
                                letter-spacing: 0.15px;

                                /* Firefox */
                                &::-webkit-scrollbar {
                                    display: none;
                                    /* for Chrome, Safari, and Opera */
                                }

                                * {
                                    font-family: sans-serif;
                                    font-size: 18px !important;
                                }
                            }

                            ol {
                                font-family: sans-serif;
                                margin-top: 32px;

                                li {
                                    color: black;
                                    opacity: 1;
                                    font-family: sans-serif;
                                    margin-bottom: 32px;
                                    font-size: 22px;
                                }
                            }

                            img {
                                width: 100%;
                                object-fit: contain;
                                display: block;
                            }

                            iframe {
                                width: calc(100%);
                                height: 550px !important;
                                margin-top: 15px;

                                @media screen and (min-width: 1400px) {
                                    height: 550px !important;
                                }

                                @media screen and (min-width: 1200px) and (max-width: 1400px) {
                                    height: 470px !important;
                                }

                                @media screen and (min-width: 992px) and (max-width: 1200px) {
                                    height: 398px !important;
                                }

                                @media screen and (min-width: 768px) and (max-width: 992px) {
                                    height: 400px !important;
                                }

                                @media screen and (min-width: 576px) and (max-width: 768px) {
                                    height: 300px !important;
                                }

                                @media screen and (max-width: 576px) {
                                    width: calc(100vw - 30px);
                                    height: calc((100vw - 30px) * (9 / 16)) !important;
                                }
                            }
                        }
                    }

                }
            }
        }
    }

    .side-container {
        position: relative;

        * {
            font-family: sans-serif !important;
            letter-spacing: -1px;
        }

        min-height: 100vh;
        background-color: white;
        padding: 70px 30px 15px 30px !important;

        .side-container-education-promotion-container {

            .side-container-education-promotion {
                margin-top: 45px;
                box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);
                -webkit-box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);
                -moz-box-shadow: -1px -1px 12px -1px rgba(0, 0, 0, 0.28);


                .side-container-education-promotion-cover {
                    width: 100%;
                    height: auto;
                    object-fit: contain;
                }

                h3 {
                    color: black;
                    text-decoration: none;
                    text-align: center;
                    margin-top: 20px;
                    font: 700 19.5px/25px HeronSansCondLight;
                }

                .side-container-education-promotion-icon {
                    width: 100%;
                    height: 55px;
                    text-align: center;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    i {
                        margin-top: -20px;
                        color: black;
                    }
                }
            }
        }

        a {
            text-decoration: none !important;

            &:hover {
                text-decoration: none !important;
            }

            &:hover {
                text-decoration: underline !important;
                color: black;
            }

            .blog-card {
                min-height: 500px;
                width: 18rem;
                border-radius: 6px;
                padding: 0px !important;
                display: inline-block !important;
                margin: 20px 0px 20px 0px;
                width: 100%;
                justify-content: center;
                display: flex;
                white-space: normal;
                box-shadow: 1px 1px 153px -95px rgba(0, 0, 0, 0.38);
                -webkit-box-shadow: 1px 1px 153px -95px rgba(0, 0, 0, 0.38);
                -moz-box-shadow: 1px 1px 153px -95px rgba(0, 0, 0, 0.38);
                //box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                //-webkit-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);
                //-moz-box-shadow: 4px 3px 61px -32px rgba(0, 0, 0, 0.75);

                img {
                    cursor: pointer;
                }

                .blog-body {
                    min-height: 200px;
                    max-height: 430px;
                    overflow: hidden;
                    text-overflow: ellipsis;

                    .blog-information {
                        font-family: inherit;
                        color: #8a8a8a;
                        font-weight: 400;
                        letter-spacing: 0.25px;
                        font-size: 16.5px;
                        height: 19px;
                        width: 100%;

                        .blog-author {
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            padding-right: 3px;
                        }

                        .blog-pub-date {
                            position: absolute;
                            right: 15px;
                            bottom: 15px;
                            padding-left: 3px;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                    }

                    .blog-title {
                        font-family: inherit;
                        font-weight: bold;
                        letter-spacing: -0.5px;
                        display: -webkit-box;
                        height: 62px;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        font-family: sans-serif;
                        font-size: 22px;
                        line-height: 31px;
                    }

                    .blog-content {
                        font-family: inherit;
                        font-size: 18px;
                        color: #555555;
                        font-weight: normal;
                        letter-spacing: -0.25px;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        -webkit-line-clamp: 4;
                        -webkit-box-orient: vertical;
                        display: -webkit-box;
                        line-height: 1.5;
                        max-height: 300px;
                        margin-top: 25px;
                    }

                    hr {
                        margin: 0px -10px 0px -10px !important;
                    }

                    .readBtn {
                        width: 100%;
                        height: 50px;
                        position: relative;
                        margin: 5px 0px 5px 0px;
                        top: 5px;
                        font-size: 18px;
                        letter-spacing: 1px;
                        text-decoration: none;
                        font-weight: bold;
                        background-color: #000000;
                        color: white;
                        outline: none;
                        border: none !important;
                        border-radius: 3px;
                    }
                }
            }
        }
    }
}